import React from 'react';
import { Modal } from "react-bootstrap";

function CustomModal(props) {
    const {show, handleClose, children, heading = '', size = 'lg'} = props;
    return (
            <Modal show={show} size={size} onHide={handleClose} className="custom-modal">
                <Modal.Header>
                    <Modal.Title>{heading}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {children}
                </Modal.Body>
            </Modal>
    );
}

export default CustomModal;