import React, { Component } from 'react';
import { Header, Footer, Sidebar, LoginFormModal, SignUpFormModal, GenericModal, ForgetPasswordFormModal } from './uiComponents';
import Routing from './routing';
import { HashRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { changeTheme } from './store/actions/Common';
import { login, signUp, setAuthData } from './store/actions/Auth';
import { getValueFromLS } from './store/libs/localstorage';
import { AT, UD } from './constants'

class App extends Component {
    state = {
        isLoginModalVisible: false,
        isSignUpModalVisible: false,
        isForgetPasswordModalVisible: false,
        isOpen: false,
    };

    componentDidMount() {
        const { setAuthData } = this.props;
        const token = getValueFromLS(AT), user = getValueFromLS(UD);
        if (token && user._id) setAuthData(user)
    }

    toggleModal = (key) => () => {
     // if(key){
         console.log('key', key)
     // }   else {
         this.setState({ [key]: !this.state[key] })
     // }
    }

    render() {
        const { changeTheme, theme } = this.props;
        const { isOpen, isLoginModalVisible, isSignUpModalVisible, isForgetPasswordModalVisible } = this.state;
        const updateTheme = (theme === 'normal-theme') ? 'dark-theme' : 'normal-theme';

        return (
            <HashRouter>
                <div className={`responsive-web app ${(isOpen ? 'open-drawer' : '')} ${theme}`}>
                    <Header
                        loginModalToggle={this.toggleModal('isLoginModalVisible')}
                        signUpModalToggle={this.toggleModal('isSignUpModalVisible')}
                        changeTheme={() => changeTheme(updateTheme)}
                    />
                    <GenericModal />

                    <div className="contain-with-sidebar">
                        <div className="sidebar-container">
                            <Sidebar
                                isOpen={isOpen}
                                className={isOpen ? 'isOpen' : ''}
                                toggle={this.toggleModal('isOpen')}
                            />
                        </div>
                        <div className="contain-container">
                            <Routing />
                        </div>
                    </div>

                    <Footer />

                    <LoginFormModal
                        isVisible={isLoginModalVisible}
                        handleClose={this.toggleModal('isLoginModalVisible')}
                        forgetPasswordFunc={()=> {
                            this.setState({
                                isLoginModalVisible: false,
                                isForgetPasswordModalVisible: true
                            })
                        }}
                    />

                    <SignUpFormModal
                        isVisible={isSignUpModalVisible}
                        handleClose={this.toggleModal('isSignUpModalVisible')}
                    />

                    <ForgetPasswordFormModal
                        isVisible={isForgetPasswordModalVisible}
                        handleClose={this.toggleModal('isForgetPasswordModalVisible')}
                    />

                </div>
            </HashRouter>
        );
    }
}


const mapStateToProps = ({ common }) => ({
    theme: common.theme
})

const mapDispatchToProps = { login, signUp, changeTheme, setAuthData };

export default connect(mapStateToProps, mapDispatchToProps)(App);
