import React, {useEffect, useState} from 'react';
import {Tab, Nav, Form, Button, Image, InputGroup} from "react-bootstrap";
import {useDispatch, useSelector} from "react-redux";
import { Formik } from 'formik';
import * as yup from 'yup';
import {Col} from "react-bootstrap/esm";

const checkLength = (minlength, value) => !(!value || value.length !== minlength);

const changePasswordSchema = yup.object().shape({
    oldPassword: yup
        .string()
        .label('Old Password')
        .required()
        .min(2, 'Seems a bit short...')
        .max(10, 'We prefer insecure system, try a shorter password.'),
    newPassword: yup
        .string()
        .label('New Password')
        .required()
        .min(2, 'Seems a bit short...')
        .max(10, 'We prefer insecure system, try a shorter password.'),
    confirmPassword: yup
        .string()
        .required()
        .label('Confirm password')
        .test('passwords-match', 'Passwords must match ya fool', function(value) {
            return this.parent.newPassword === value;
        })
});

const CardForm = () => {
    const [card_number, setCardNumber] = useState('');
    const [cvc, setCvc] = useState('');
    const [expiration_date, setExpirationDate] = useState('');
    const [isFormValid, setIsFormValid] = useState(false);

    const dipatch = useDispatch();
    const { isLoading } = useSelector(({ user }) => (user));

    // useEffect(() => setIsFormValid(false));

    /**
     * Getting fields data from input box
     * @param {function} onChange - function for setting field data into the states
     */
    const handleChange = (onChange) => ({ target: { value } }) => onChange && onChange(value);

    /**
     * Login user after validating data
     *
     * @param {Event} e
     */
    const handleSubmit = (e) => {
        e.preventDefault();
        const form = e.currentTarget;
        if (!(form.checkValidity() && checkLength(16, card_number) && checkLength(3, cvc))) {
            setIsFormValid(true);
            e.stopPropagation();
        } else {
            setIsFormValid(false)
        }
    }

    return (
        <Form noValidate validated={isFormValid} onSubmit={handleSubmit}>
            <Form.Group className="hide-input-icon">
                <Form.Label> Card number </Form.Label>
                <div className="input-with-text">
                    <Form.Control
                        required
                        value={card_number}
                        onChange={handleChange(setCardNumber)}
                        isInvalid={!checkLength(16, card_number)}
                        type="number"
                        placeholder="0000 0000 0000 0000"/>
                    <Form.Control.Feedback type="invalid">
                        Please enter a valid card number It should be at least 16 digits.
                    </Form.Control.Feedback>
                </div>
            </Form.Group>
            <Form.Group className="hide-input-icon">
                <Form.Label> Expiration date </Form.Label>
                <div className="input-with-text">
                    <Form.Control
                        required
                        value={expiration_date}
                        onChange={handleChange(setExpirationDate)}
                        isInvalid={!checkLength(5, expiration_date)}
                        type="text"
                        placeholder="MM / YY"/>
                    <Form.Control.Feedback type="invalid">
                        Please enter a valid expiration date.
                    </Form.Control.Feedback>
                </div>
            </Form.Group>
            <Form.Group className="hide-input-icon">
                <Form.Label> CVC </Form.Label>
                <div className="input-with-text">
                    <Form.Control
                        required
                        value={cvc}
                        isInvalid={!checkLength(3, cvc)}
                        onChange={handleChange(setCvc)}
                        type="number"
                        placeholder="CVC"/>
                    <Form.Control.Feedback type="invalid">
                        Please enter a valid cvc It should be at least 3 digits.
                    </Form.Control.Feedback>
                </div>
            </Form.Group>
            <div className="button-container">
                <Button className="normal-button" type="submit">
                    Save
                </Button>
            </div>
        </Form>
    )
};

const ChangePassword = () => {

    return (
        <Formik
            validationSchema={changePasswordSchema}
            onSubmit={(values, {setSubmitting, resetForm}) => {
                // // When button submits form and form is in the process of submitting, submit button is disabled
                // setSubmitting(true);
                //
                // // Resets form after submission is complete
                // resetForm();
                //
                // // Sets setSubmitting to false after form is reset
                // setSubmitting(false);
            }}
            initialValues={{
                oldPassword: '',
                newPassword: '',
                confirmPassword: ''
            }}>
            {({
                  handleSubmit,
                  handleChange,
                  handleBlur,
                  values,
                  touched,
                  isValid,
                  errors
              }) => {
                console.log(errors)
                return (
                    <Form onSubmit={handleSubmit}>
                        <Form.Group>
                            <Form.Label> Old password </Form.Label>
                            <div className="input-with-text">
                                <Form.Control
                                    required
                                    type="text"
                                    name="oldPassword"
                                    value={values.oldPassword}
                                    isValid={!!errors.oldPassword}
                                    isInvalid={errors.oldPassword}
                                    onBlur={handleBlur}
                                    onChange={handleChange}
                                    className={touched.oldPassword && errors.oldPassword ? "error" : null}
                                    placeholder="******"/>
                                <Form.Control.Feedback type="invalid">
                                    {errors.oldPassword}
                                </Form.Control.Feedback>
                            </div>
                        </Form.Group>
                        <Form.Group>
                            <Form.Label> New password </Form.Label>
                            <div className="input-with-text">
                                <Form.Control
                                    required
                                    name="newPassword"
                                    type="text"
                                    value={values.newPassword}
                                    isValid={!!errors.newPassword}
                                    isInvalid={errors.newPassword}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    className={touched.newPassword && errors.newPassword ? "error" : null}
                                    placeholder="******"/>
                                <Form.Control.Feedback type="invalid">
                                    {errors.newPassword}
                                </Form.Control.Feedback>
                            </div>
                        </Form.Group>
                        <Form.Group>
                            <Form.Label> Confirm password </Form.Label>
                            <div className="input-with-text">
                                <Form.Control
                                    required
                                    name="confirmPassword"
                                    value={values.confirmPassword}
                                    isValid={!!errors.confirmPassword}
                                    isInvalid={errors.confirmPassword}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    className={touched.confirmPassword && errors.confirmPassword ? "error" : null}
                                    type="text"
                                    placeholder="******"/>
                                <Form.Control.Feedback type="invalid">
                                    {errors.confirmPassword}
                                </Form.Control.Feedback>
                            </div>
                        </Form.Group>

                        <div className="button-container">
                            <Button className="normal-button" type="submit">
                                Change
                            </Button>
                        </div>
                    </Form>
                )
            }}
        </Formik>
    );
};

