import { OPEN_GENERIC_MODAL, CLOSE_GENERIC_MODAL } from '../constants/Modal';

export default (state = {}, { type, payload }) => {
    switch (type) {
        case OPEN_GENERIC_MODAL:
            return { ...state, isModalOpen: true, ...payload };

        case CLOSE_GENERIC_MODAL:
            return { ...state, isModalOpen: false, ...payload };

        default:
            return state;
    }
};