import {
    SIGNUP_SUCCESS,
    SIGNUP_START,
    LOGIN_START,
    LOGIN_SUCCESS,
    LOGIN_FAILURE,
    SIGNUP_FAILURE,
    SET_AUTH_DATA,
    LOGGED_OUT_START,
    LOGGED_OUT_SUCCESS,
    LOGGED_OUT_FAILURE,
    FORGET_PASSWORD_START,
    FORGET_PASSWORD_SUCCESS,
    FORGET_PASSWORD_FAILURE
} from '../constants/Auth';
import { OPEN_GENERIC_MODAL } from '../constants/Modal';
import { post, get } from '../helper';
import { setValueInLS, clearLS } from '../libs/localstorage';
import { AT, UD } from '../../constants';

/**
 * Login user and save data to the LS
 * 
 * @param {{ email:String, password: String }} payload 
 * @param { function } callback
 */
export const login = (payload, callback) => async (dispatch) => {
    dispatch({ type: LOGIN_START });
    try {
        const { data } = await post('login', payload)
        if (data && data.body) {
            const authData = JSON.parse(data.body)
            if (authData.code) {
                dispatch({ type: LOGIN_FAILURE, payload: { error: { message: authData.message } } });
                dispatch({
                    type: OPEN_GENERIC_MODAL, payload: {
                        title: 'Error',
                        message: authData.message
                    }
                });
                return
            }
            const { token, user } = (authData && authData.data) || { token: '', user: {} }
            setValueInLS(AT, token)
            setValueInLS(UD, user)
            dispatch({ type: LOGIN_SUCCESS, payload: { user } })
            callback && callback()
            return;
        }
        dispatch({ type: LOGIN_FAILURE });
    } catch (error) {
        dispatch({ type: LOGIN_FAILURE, payload: { error } });
    }
};

/**
 * Sign Up User
 * 
 * @param {{ userName:string, email:string, phoneNumber:string, password:string }} payload 
 * @param {function} callback
 */
export const signUp = (payload, callback) => async (dispatch) => {
    dispatch({ type: SIGNUP_START });
    try {
        const { data } = await post('signup', payload)
        if (data && data.body) {
            const { status, message } = JSON.parse(data.body) || { status: 500 }
            if (status === 200) {
                dispatch({ type: SIGNUP_SUCCESS })
                dispatch({
                    type: OPEN_GENERIC_MODAL, payload: {
                        title: 'Email Verification',
                        message: 'A verification email has been sent to your Email Address. Please open the verification link to login to your account.'
                    }
                });
                callback && callback()
                return;
            } else {
                dispatch({
                    type: OPEN_GENERIC_MODAL, payload: {
                        title: 'Error',
                        message
                    }
                });
            }
        }
        dispatch({ type: SIGNUP_FAILURE });
    } catch (error) {
        dispatch({ type: SIGNUP_FAILURE, payload: { error } });
    }
};

/**
 * Set Auth Data
 * 
 * @param {object} user
 */
export const setAuthData = (user) => (dispatch) => {
    dispatch({ type: SET_AUTH_DATA, payload: { user } })
}

/**
 * Loggout user
 * 
 * @param {string} userId
 */
export const loggedOut = (userId) => async (dispatch) => {
    dispatch({ type: LOGGED_OUT_START });
    try {
        await get('logout', { headers: { _id: userId } })
        clearLS();
        dispatch({ type: LOGGED_OUT_SUCCESS });
    } catch (err) {
        dispatch({ type: LOGGED_OUT_FAILURE });
    }
}

/**
 * forget password user and save data to the LS
 *
 * @param {{ email:String }} payload
 * @param { function } callback
 */
export const forgetPassword = (payload, callback) => async (dispatch) => {
    dispatch({ type: FORGET_PASSWORD_START });
    try {
        const { data } = await post('forgetPassword', payload);
        if (data && data.body) {
            const { status, message } = JSON.parse(data.body);
            if (status === 200) {
                dispatch({ type: FORGET_PASSWORD_SUCCESS });
                callback && callback();
                dispatch({ type: OPEN_GENERIC_MODAL, payload: { title: 'Success', message: 'A verification email has been sent to your email.' } });
            } else {
                dispatch({ type: FORGET_PASSWORD_FAILURE});
                dispatch({ type: OPEN_GENERIC_MODAL, payload: { title: 'Error', message } });
            }
        }
    } catch (error) {
        dispatch({ type: FORGET_PASSWORD_FAILURE });
    }
};