import React, { useState, useEffect } from 'react';
import { CustomModal } from '..';
import {Button, Form, Spinner} from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { signUp } from '../../store/actions/Auth';

const SignUpFormModal = ({
    handleClose,
    isVisible,
}) => {
    const [email, setEmail] = useState('')
    const [userName, setUserName] = useState('')
    const [phoneNumber, setPhoneNumber] = useState('')
    const [password, setPassword] = useState('')
    const [isFormValid, setIsFormValid] = useState(false)

    const dipatch = useDispatch()
    const { isLoading } = useSelector(({ user }) => (user))

    useEffect(() => setIsFormValid(false), [isVisible])

    /**
     * Getting fields data from input box
     * @param {function} onChange - function for setting field data into the states
     */
    const handleChange = (onChange) => ({ target: { value } }) => {
        onChange && onChange(value)
    }

    /**
     * Sign Up user after validating data
     * 
     * @param {Event} e
     */
    const handleSubmit = (e) => {
        e.preventDefault()
        const form = e.currentTarget;
        if (!form.checkValidity()) {
            e.stopPropagation();
            setIsFormValid(true)
        } else {
            setIsFormValid(false)
            dipatch(signUp({ userName, email, phoneNumber, password }, handleClose))
        }
    }

    return (
        <>
            <CustomModal
                show={isVisible}
                heading="Sign Up"
                handleClose={handleClose}>
                <Form noValidate validated={isFormValid} onSubmit={handleSubmit}>
                    <Form.Group controlId="formPlaintextUsername">
                        <Form.Control
                            required
                            type="text"
                            value={userName}
                            name="userName"
                            placeholder="Enter username"
                            onChange={handleChange(setUserName)}
                        />
                        <Form.Control.Feedback type="invalid">
                            Please enter a valid Username.
                    </Form.Control.Feedback>
                    </Form.Group>

                    <Form.Group controlId="formPlaintextEmail">
                        <Form.Control
                            required
                            type="email"
                            name="email"
                            value={email}
                            onChange={handleChange(setEmail)}
                            placeholder="Enter Email Address"
                        />
                        <Form.Control.Feedback type="invalid">
                            Please enter a valid Email Address.
                    </Form.Control.Feedback>
                    </Form.Group>

                    <Form.Group controlId="formPlaintextPhone">
                        <Form.Control
                            required
                            type="number"
                            name="phoneNumber"
                            value={phoneNumber}
                            placeholder="Phone"
                            onChange={handleChange(setPhoneNumber)}
                        />
                        <Form.Control.Feedback type="invalid">
                            Please enter a valid Phone Number.
                    </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group controlId="formPlaintextPassword">
                        <Form.Control
                            required
                            type="password"
                            name="password"
                            value={password}
                            placeholder="Password"
                            onChange={handleChange(setPassword)}
                        />
                        <Form.Control.Feedback type="invalid">
                            Please enter a Password.
                    </Form.Control.Feedback>
                    </Form.Group>

                    <div className="modal-button">
                        <Button disabled={isLoading} className="normal-button" type="submit">
                            {isLoading ? <Spinner
                                as="span"
                                animation="grow"
                                size="sm"
                                role="status"
                                aria-hidden="true"
                            /> : 'Sign Up' }
                </Button>
                    </div>
                </Form>
            </CustomModal>
        </>
    )
}

export default SignUpFormModal;