import React, { useState, useEffect } from 'react';
import { CustomModal } from '..';
import {Button, Form, Spinner} from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { login } from '../../store/actions/Auth';

const LoginFormModal = ({
    handleClose,
    isVisible,
    forgetPasswordFunc
}) => {
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [isFormValid, setIsFormValid] = useState(false)

    const dipatch = useDispatch()
    const { isLoading } = useSelector(({ user }) => (user))

    useEffect(() => setIsFormValid(false), [isVisible])

    /**
     * Getting fields data from input box
     * @param {function} onChange - function for setting field data into the states
     */
    const handleChange = (onChange) => ({ target: { value } }) => {
        onChange && onChange(value)
    }

    /**
     * Login user after validating data
     * 
     * @param {Event} e
     */
    const handleSubmit = (e) => {
        e.preventDefault()
        const form = e.currentTarget;
        if (!form.checkValidity()) {
            e.stopPropagation();
            setIsFormValid(true)
        } else {
            setIsFormValid(false)
            dipatch(login({ email, password }, handleClose))
        }
    }

    return (
        <CustomModal
            show={isVisible}
            handleClose={handleClose}
            heading="Login">

            <Form noValidate validated={isFormValid} onSubmit={handleSubmit}>
                <Form.Group controlId="formPlaintextEmail">
                    <Form.Control
                        required
                        type="email"
                        name="email"
                        value={email}
                        placeholder="Email Address"
                        onChange={handleChange(setEmail)}
                    />
                    <Form.Control.Feedback type="invalid">
                        Please enter a valid email.
                    </Form.Control.Feedback>
                </Form.Group>

                <Form.Group controlId="formPlaintextPassword">
                    <Form.Control
                        required
                        type="password"
                        name="password"
                        value={password}
                        placeholder="Password"
                        onChange={handleChange(setPassword)}
                    />
                    <Form.Control.Feedback type="invalid">
                        Please enter a valid password.
                    </Form.Control.Feedback>
                </Form.Group>

                <p className="forget-button">
                    <button type="button" onClick={forgetPasswordFunc}>Forget password?</button>
                </p>

                <div className="modal-button">
                    <Button disabled={isLoading} className="normal-button" type="submit">
                        {isLoading ? <Spinner
                            as="span"
                            animation="grow"
                            size="sm"
                            role="status"
                            aria-hidden="true"
                        /> : 'Login' }
                    </Button>
                </div>
            </Form>
        </CustomModal>
    )
}

export default LoginFormModal;
