import React from 'react';
import { CustomModal } from '..';
import { Button } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { closeGenericModal } from '../../store/actions/Modal'

const GenericModal = () => {
    const dispatch = useDispatch()
    const { isModalOpen, title, message } = useSelector(({ modal }) => (modal))

    const handleClose = () => dispatch(closeGenericModal())

    return (
        <CustomModal
            heading={title}
            show={isModalOpen}
            handleClose={handleClose} >
            <div className="error-container">
                <p>{message}</p>
            </div>
            <div className="modal-button">
                <Button onClick={handleClose} className="normal-button" type="submit">
                    Close
                </Button>
            </div>
        </CustomModal >
    )
}

export default GenericModal;