import React from 'react';
import { Button, Dropdown, Form, FormControl, Image, Nav, Navbar } from 'react-bootstrap';
import { MdSearch, MdContentCopy } from 'react-icons/md';
import { GoGlobe } from 'react-icons/go';
import { IoMdLogOut, IoIosArrowDown, IoIosDesktop } from 'react-icons/io';
import { FiMoon } from 'react-icons/fi';
import { GoGear } from 'react-icons/go';
import { FaRegCompass } from 'react-icons/fa';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux'
import { loggedOut } from '../../store/actions/Auth'

const Header = ({ loginModalToggle, signUpModalToggle, changeTheme }) => {

    const { isLoggedIn, user } = useSelector(({ user }) => (user))
    const dispatch = useDispatch()

    const handleLogout = () => {
        dispatch(loggedOut(user._id))
    }

    return (
        <div className="header">
            <Navbar expand="lg">

                <Navbar.Brand>
                    <Link to="/">
                        <Image src={require('../../assets/images/logo.png')} className="logo" />
                    </Link>
                </Navbar.Brand>

                <Navbar id="basic-navbar-nav">
                    <Nav className="align-items-center">
                        <Nav.Link href="#discover">
                            <span>Discover</span>
                            <FaRegCompass />
                        </Nav.Link>

                        <Nav.Link href="#browse">
                            <span>Browse</span>
                            <MdContentCopy />
                        </Nav.Link>

                        <Dropdown>
                            <Dropdown.Toggle>
                                <Image src={require('../../assets/images/icon-home/menu.png')} />
                            </Dropdown.Toggle>

                            <Dropdown.Menu>
                                <Link to="/about">About</Link>
                                <Dropdown.Item href="#/action-2">Community Guidelines</Dropdown.Item>
                                <Dropdown.Item href="#/action-3">Cookie Policy</Dropdown.Item>
                                <Link to="/help">Help</Link>
                                <Link to="/privacy">Privacy Policy</Link>
                            </Dropdown.Menu>
                        </Dropdown>
                    </Nav>

                    <Form inline className="header-search-container">
                        <FormControl type="text" placeholder="Search" />
                        <button> <MdSearch /> </button>
                    </Form>

                    <Nav className="align-items-center m-0">
                        <div className="select-language dropdown">
                            <GoGlobe />
                            <div className=" google" id="google_translate_element" />
                            <IoIosArrowDown className="down-arrow" />
                        </div>
                        {isLoggedIn ?
                            null
                            :
                            <>
                                <Button className="normal-button black-button" onClick={loginModalToggle}>Login</Button>
                                <Button className="normal-button" onClick={signUpModalToggle}>Sign Up</Button>
                            </>
                        }
                        {isLoggedIn ?
                            <Dropdown className="mr-0 setting-dropdown">
                                <Dropdown.Toggle className="userAvatar">
                                    <Image src={require('../../assets/images/icon-home/avatar.png')} className="userAvatarImage" />
                                </Dropdown.Toggle>

                                <Dropdown.Menu>
                                    <a href='#'>
                                        <FiMoon />
                                        <Form.Check
                                            type="switch"
                                            id="custom-switch"
                                            label="Drak Theme"
                                            onClick={() => changeTheme()}
                                        />
                                    </a>
                                    <Link to="/my-channel"> <IoIosDesktop/> My Channel </Link>
                                    <Link to="/settings"> <GoGear /> Settings </Link>
                                    <Dropdown.Item onClick={handleLogout}> <IoMdLogOut /> Log Out </Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                            :
                            null
                        }
                    </Nav>
                </Navbar>
            </Navbar>
        </div>
    );
}

export default Header;
