import React, {Fragment} from 'react';
import ReactDOM from 'react-dom';
import './assets/scss/index.scss';
import App from './App';
import * as serviceWorker from './serviceWorker';
import {interceptor} from "./store/helper";
import {Provider, useSelector} from "react-redux";
import {store} from "./store";
import { Loading } from "./uiComponents";
interceptor();

function JASP() {
    const { logoutLoading } = useSelector(({ user }) => (user));

    return(
       <Fragment>
            <App/>
            <Loading isShow={logoutLoading}/>
       </Fragment>
    )
};

ReactDOM.render( (<Provider store={store}> <JASP />  </Provider>), document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
