import React from 'react';
import Slider from "react-slick";
import { Image } from "react-bootstrap";
import {slider} from "../../data/header-section";

const headerSectionSliderSettings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
};

const renderSlides = (slider) => {
    if(slider){
        return slider.map((slide, index) => (
            <div key={index}>
                <div className="header-section-slide">
                    <Image src={slide.image} className="header-section-slide-image"/>
                </div>
            </div>
        ))
    } else {
        return null;
    }
};

function HeaderSection() {
    return (
        <section className="section header-section">
            <Slider {...headerSectionSliderSettings} className="header-section-slider">
                {renderSlides(slider)}
            </Slider>
        </section>
    );
}

export default HeaderSection;
