import React from 'react';
import { Card } from '../../uiComponents';
import Slider from "react-slick";
import {Button} from "react-bootstrap";

const recommendedSectionSlider = {
    dots: false,
    arrows: true,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 3,
    responsive: [
        {
            breakpoint: 1024,
            settings: {
                slidesToShow: 3,
                slidesToScroll: 3
            }
        },
        {
            breakpoint: 992,
            settings: {
                slidesToShow: 2,
                slidesToScroll: 2
            }
        },
        {
            breakpoint: 600,
            settings: {
                slidesToShow: 1,
                slidesToScroll: 1
            }
        }
    ]
};
const categoriesSectionSlider = {
    dots: false,
    arrows: true,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 4,
    responsive: [
        {
            breakpoint: 1024,
            settings: {
                slidesToShow: 3,
                slidesToScroll: 3
            }
        },
        {
            breakpoint: 992,
            settings: {
                slidesToShow: 2,
                slidesToScroll: 2
            }
        },
        {
            breakpoint: 600,
            settings: {
                slidesToShow: 1,
                slidesToScroll: 1
            }
        }
    ]
};

const renderRecommendedSectionSlider = (data) => {
    if(data){
        return data.map((obj, index) => <Card key={index} data={obj}/>)
    } else {
        return null
    }
};

function RecommendedSection(props) {
    const {
        firstTitle = 'Recommended',
        title = '',
        list = [],
        secondList = [],
        buttonTitle,
        buttonFunction,
        className = ''
    } = props;
    let sliderConfig =  props.sliderConfig === '4Slides' ? categoriesSectionSlider : recommendedSectionSlider;
    return (
        <section className={`section slider-section ${className}`}>
            <h1 className="heading">{firstTitle} <span>{title}</span></h1>
            <Slider {...sliderConfig}>
                {renderRecommendedSectionSlider(list)}
            </Slider>
            {secondList ?
                <Slider {...sliderConfig}>
                    {renderRecommendedSectionSlider(secondList)}
                </Slider> : null}

            {buttonTitle ? <div className="section-button-container">
                <Button className="normal-button" onClick={buttonFunction}>{buttonTitle}</Button>
            </div> : null}

        </section>
    );
}


export default RecommendedSection;
