import React from "react";
import {Image} from "react-bootstrap";

function Card({data}) {
    return (
        <div className="c-card">
            <div className="c-card-header">
                {data.islive ? <span className="tag is-live ">Live</span> : null}
                {data.viewers ? <span className="tag viewers ">{data.viewers}</span> : null}
                <Image src={data.coverImage} className="c-card-header-image"/>
            </div>
            <div className="c-card-body">
                {data.userImage ? <Image src={data.userImage} className="c-card-user-image"/> : null}
                <div className="c-card-body-content">
                    <h5>{data.title}</h5>
                    <p>{data.paragraph}</p>
                </div>
            </div>
        </div>
    );
}
export default Card;