function generateHexString(length) {
    let ret = "";
    while (ret.length < length) {
        ret += Math.random().toString(16).substring(2);
    }
    return ret.substring(0,length);
}

function renderFollowersVideos(data) {
    if (!(data && data.length)) {
        return (
            <div className="empty">
                <p> No videos found </p>
            </div>
        )
    } else {
        return data.map((video, index) => {
            return (
                <div key={index} className="video">
                    <ReactPlayer
                        className='react-player'
                        url={video.url}
                        width='100%'
                        height='100%'
                        controls
                    />
                </div>
            )
        })
    }
}

const LiveFormModal = ({ handleClose, isVisible, heading, streamKey }) => {
    const [title, setTitle] = useState('');
    const [category, setCategory] = useState('');
    const [isFormValid, setIsFormValid] = useState(false);
    const dipatch = useDispatch();
    const { isLoading } = useSelector(({ user }) => (user));
    useEffect(() => setIsFormValid(false), [isVisible]);

    /**
     * Getting fields data from input box
     * @param {function} onChange - function for setting field data into the states
     */
    const handleChange = (onChange) => ({ target: { value } }) => onChange && onChange(value);

    /**
     * Login user after validating data
     *
     * @param {Event} e
     */
    const handleSubmit = (e) => {
        e.preventDefault()
        const form = e.currentTarget;
        if (!form.checkValidity()) {
            e.stopPropagation();
            setIsFormValid(true)
        } else {
            setIsFormValid(false)

        }
    }

    return (
        <CustomModal
            show={isVisible}
            handleClose={handleClose}
            heading={heading}>

            <Form noValidate validated={isFormValid} onSubmit={handleSubmit}>
                <Form.Group>
                    <Form.Label>Channel Title</Form.Label>
                    <Form.Control
                        required
                        type="title"
                        name="title"
                        value={title}
                        placeholder="Title"
                        onChange={handleChange(setTitle)}
                    />
                    <Form.Control.Feedback type="invalid">
                        Please enter a title.
                    </Form.Control.Feedback>
                </Form.Group>

                <Form.Group>
                    <Form.Label>Channel Category</Form.Label>
                    <Form.Control
                        required
                        as="select"
                        value={category}
                        onChange={handleChange(setCategory)}>
                        <option key={'empty'} value={''}> Select Category </option>
                        <option key={'1'} value={'1'}>1</option>
                        <option key={'2'} value={'2'}>2</option>
                    </Form.Control>
                    <Form.Control.Feedback type="invalid">
                        Please select a category.
                    </Form.Control.Feedback>
                </Form.Group>

                <Form.Group className="show-message">
                    <Form.Label>Channel Stream Key</Form.Label>
                    <Form.Control
                        type="text"
                        name="password"
                        value={streamKey}
                        readOnly
                        placeholder="Stream Key"
                    />
                    <Form.Control.Feedback>
                        Copy and paste stream key in OBS.
                    </Form.Control.Feedback>
                </Form.Group>


                <div className="modal-button">
                    <Button disabled={isLoading} className="normal-button" type="submit">
                        GO
                    </Button>
                </div>
            </Form>
        </CustomModal>
    )
};

