import {
    SIGNUP_API,
    LOGIN_START,
    LOGIN_SUCCESS,
    LOGIN_FAILURE,
    SIGNUP_START,
    SIGNUP_FAILURE,
    SIGNUP_SUCCESS,
    SET_AUTH_DATA,
    LOGGED_OUT_START,
    LOGGED_OUT_SUCCESS,
    LOGGED_OUT_FAILURE,
    FORGET_PASSWORD_START,
    FORGET_PASSWORD_SUCCESS,
    FORGET_PASSWORD_FAILURE
} from "../constants/Auth";

const initialState = {
    user: {},
    error: {},
    isLoggedIn: false,
    isLoading: false,
    logoutLoading: false
};

export default (state = initialState, { type, payload = {} }) => {
    switch (type) {

        case SIGNUP_API:
            return { ...state, user: {}, loginLoading: false };

        case LOGIN_START:
            return { ...state, isLoading: true, error: {}, user: {} };

        case LOGIN_SUCCESS:
            return { ...state, isLoading: false, isLoggedIn: true, user: payload.user || {}, error: {} };

        case LOGIN_FAILURE:
            return { ...state, isLoading: false, isLoggedIn: false, error: payload.error || {}, user: {} };

        case SIGNUP_START:
            return { ...state, isLoading: true, error: {} };

        case SIGNUP_SUCCESS:
            return { ...state, isLoading: false, isLoggedIn: true, error: {} };

        case SIGNUP_FAILURE:
            return { ...state, isLoading: false, isLoggedIn: false, error: payload.error || {} };

        case SET_AUTH_DATA:
            return { ...state, isLoading: false, isLoggedIn: true, user: payload.user || {} };

        case LOGGED_OUT_START:
            return { ...state, logoutLoading: true };
        case LOGGED_OUT_SUCCESS:
            return { ...state, logoutLoading: false, isLoggedIn: false, user: {} };
        case LOGGED_OUT_FAILURE:
            return { ...state, logoutLoading: false };

        case FORGET_PASSWORD_START:
            return { ...state, isLoading: true, error: {} };
        case FORGET_PASSWORD_SUCCESS:
            return { ...state, isLoading: false, error: {} };
        case FORGET_PASSWORD_FAILURE:
            return { ...state, isLoading: false, error: payload.error || {} };

        default:
            return state;
    }
};