import React from 'react';
import { HeaderSection, RecommendedSection } from '../../uiComponents';
import {categories, chattingChannels, dota2Channels, fortniteChannels, liveChannels} from "../../data/recommended-data";

function Home() {
    return (
        <main className="main-wrapper">
            <HeaderSection/>
            <RecommendedSection
                title="live channels"
                list={liveChannels}
            />
            <RecommendedSection
                className="categories-section"
                title="Categories"
                list={categories}
                sliderConfig='4Slides'
            />
            <RecommendedSection
                title="Fortnite channels"
                list={fortniteChannels}
                buttonTitle='Show More'
                buttonFunction={() => alert('Fortnite channels')}
            />
            <RecommendedSection
                title="Dota 2 channels"
                list={dota2Channels}
                buttonTitle='Show More'
                buttonFunction={() => alert('Dota 2 channels')}
            />
            <RecommendedSection
                title="Chatting channels"
                list={chattingChannels}
            />
        </main>
    );
}

export default Home;
