function renderFollowersVideos(data) {
    if (!(data && data.length)) {
        return (
            <div className="empty">
                <p> No videos found </p>
            </div>
        )
    } else {
        return data.map((video, index) => {
            return (
                <div key={index} className="video">
                    <ReactPlayer
                        className='react-player'
                        url={video.url}
                        width='100%'
                        height='100%'
                        controls
                    />
                </div>
            )
        })
    }
}

